.navbar{
    display: flex;
    padding: 16px;
    justify-content:space-around;
    box-shadow: 0 1px 3px -2px black;
}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo p{
    color: #171717;
    font-size: 28px;
    font-weight: 600;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #322c2c;
    font-size: 15px;
    font-weight: bold;
}
.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    gap: 3px;
}
.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 5px;
    background-color: #FF4141;
}
.nav-login-cart{
display: flex;
align-items: center;
gap: 45px;
}
.nav-login-cart button{
    width: 157px;
    outline: none;
    height: 48px;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 20px;
    font-weight:500 ;
    background: white;
    cursor: pointer;
}
.nav-login-cart button:active{
    background-color: #171717;
    color: white;
}
.nav-cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -35px;
   margin-left: -55px;
   border-radius: 11px;
   font-size: 14px;
   background: red;
   color:white;
}